<template>
  <div id="HomePage">
    <header-component>
      <div class="rightBox">
        <div class="nameBox">
          {{ userName }}
        </div>
        <div>退出</div>
      </div>
    </header-component>
    <el-container class="container">
      <div class="pageTitle">我的问卷</div>
      <search-row @change="filterChange">
        <div class="search-row-right">
          <div class="choose-line">
            <el-dropdown trigger="click" @command="filterStar">
              <div>
                <span class="el-dropdown-link">{{ starType }}</span>
                <i class="el-icon-arrow-down el-icon--right"></i>
              </div>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="已收藏">已收藏</el-dropdown-item>
                <el-dropdown-item command="未收藏">未收藏</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-dropdown trigger="click" @command="filterStatus">
              <div>
                <span class="el-dropdown-link">{{ statusType }}</span>
                <i class="el-icon-arrow-down el-icon--right"></i>
              </div>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="未发布">未发布</el-dropdown-item>
                <el-dropdown-item command="进行中">进行中</el-dropdown-item>
                <el-dropdown-item command="暂停">暂停</el-dropdown-item>
                <el-dropdown-item command="已完成">已完成</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <el-dropdown trigger="click" @command="filterTime">
              <div>
                <span class="el-dropdown-link">{{ timeType }}</span>
                <i class="el-icon-arrow-down el-icon--right"></i>
              </div>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="时间倒序">时间倒序</el-dropdown-item>
                <el-dropdown-item command="时间正序">时间正序</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
          <div class="button-box">
            <el-button
              size="small"
              :type="usefulButton"
              :disabled="isFilter"
              @click="reFilter"
              ><span class="span">重置筛选</span></el-button
            >
            <el-button size="small" type="new"
              ><span class="span">新建问卷</span></el-button
            >
          </div>
        </div>
      </search-row>
      <el-row class="table-box">
        <el-col :offset="1" :span="22">
          <el-table
            stripe
            :data="tableDataShow"
            :header-cell-style="{
              textAlign: 'center',
              background: '#5F7880',
            }"
            :cell-style="{ textAlign: 'center' }"
            style="width: 100%"
          >
            <el-table-column prop="star" label="收藏">
              <template slot-scope="scope">
                <div>
                  <i v-if="scope.row.star" class="el-icon-star-on"></i>
                  <i v-if="!scope.row.star" class="el-icon-star-off"></i>
                </div>
              </template>
            </el-table-column>
            <el-table-column prop="name" label="问卷名称"> </el-table-column>
            <el-table-column prop="state" label="状态">
              <template slot-scope="scope">
                <el-row>
                  <el-col :span="1" :offset="6" class="circle-box">
                    <span :class="circleStatus(scope.row.state)"></span>
                  </el-col>
                  <el-col :span="12">
                    {{ statusCom(scope.row.state) }}
                  </el-col>
                </el-row>
              </template>
            </el-table-column>
            <el-table-column prop="modifiedTime" label="修改时间">
            </el-table-column>
            <el-table-column prop="number" label="作答人数"> </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text"
                  >编辑</el-button
                >
                <el-button type="text">分析</el-button>
              </template>
            </el-table-column>
            <el-table-column label="更多">
              <template slot-scope="scope">
                <el-dropdown
                  @command="handleCommand"
                  @visible-change="saveRow(scope)"
                >
                  <i class="el-icon-more"></i>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="publish">发布</el-dropdown-item>
                    <el-dropdown-item command="copy">复制</el-dropdown-item>
                    <el-dropdown-item command="delete">删除</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-container>
  </div>
</template>

<script>
import HeaderComponent from "@/components/HeaderComponent.vue";
import SearchRow from "@/components/SearchRow.vue";
import { myVote } from "../api/api.js";
export default {
  name: "HomePage",
  components: {
    HeaderComponent,
    SearchRow,
  },
  data() {
    return {
      userName: "四字名字·四字姓氏",
      tableData: [
        {
          star: true,
          name: "这是一个问卷",
          state: 1,
          modifiedTime: "2021/01/02",
          number: 1895,
        },
        {
          star: false,
          name: "这是一个问卷2",
          state: 0,
          modifiedTime: "2021/01/03",
          number: 1895,
        },
        {
          star: true,
          name: "这是一个问卷3",
          state: 1,
          modifiedTime: "2021/01/01",
          number: 1895,
        },
        {
          star: true,
          name: "这是一个问卷4",
          state: 0,
          modifiedTime: "2021/01/05",
          number: 1895,
        },
        {
          star: true,
          name: "这是一个问卷4",
          state: 1,
          modifiedTime: "2021/01/05",
          number: 1895,
        },
      ],
      tableDataShow: [
        {
          star: true,
          name: "这是一个问卷",
          state: 1,
          modifiedTime: "2021/01/02",
          number: 1895,
        },
        {
          star: false,
          name: "这是一个问卷2",
          state: 0,
          modifiedTime: "2021/01/03",
          number: 1895,
        },
        {
          star: true,
          name: "这是一个问卷3",
          state: 1,
          modifiedTime: "2021/01/01",
          number: 1895,
        },
        {
          star: true,
          name: "这是一个问卷4",
          state: 0,
          modifiedTime: "2021/01/05",
          number: 1895,
        },
        {
          star: true,
          name: "这是一个问卷4",
          state: 1,
          modifiedTime: "2021/01/05",
          number: 1895,
        },
      ],
      starType: "收藏",
      statusType: "状态",
      timeType: "修改时间",
      usefulButton: "disabled",
      isFilter: true,
      table_row: "table-row",
      inputVal: "",
      searchTempTable: [],
      saveFlag: false,
      rowData: {},
    };
  },
  created() {
    myVote().then((res) => {
      console.log(res);
    });
  },
  methods: {
    saveRow(scope) {
      // console.log(scope)
      this.rowData = scope.row;
      console.log(this.rowData);
    },
    handleCommand(command) {
      const h = this.$createElement;
      const ques_name = this.rowData["name"];
      // console.log(scope);
      switch (command) {
        case "publish":
          console.log(command);
          this.$msgbox({
            message: h("div", {}, []),
          });
          break;
        case "copy":
          console.log(command);
          break;
        case "delete":
          console.log(command);
          console.log("hello,world");
          this.$msgbox({
            message: h("div", { class: "msgbox-box" }, [
              h("span", { class: "msgbox-word" }, `是否删除【${ques_name}】?`),
              // h('div', {class: 'button-box'}, [
              //     h('el-button', {type: 'danger', click: 'try'}, '是'),
              //     h('el-button', {}, '否'),
              // ])
            ]),
            // showConfirmButton: false,
            showCancelButton: true,
            confirmButtonText: "是",
            cancelButtonText: "否",
            customClass: "msgbox-delete",
            cancelButtonClass: "cancel-button-1",
            confirmButtonClass: "confirm-button-1",
            beforeClose: (action, instance, done) => {
              if (action == "confirm") {
                instance.confirmButtonLoad = true;
                instance.confirmButtonText = "执行中";
                // 调用接口删除
                setTimeout(() => {
                  done();
                  setTimeout(() => {
                    instance.confirmButtonLoading = false;
                  }, 300);
                }, 3000);
              } else {
                done();
              }
            },
            center: true,
          });
          break;
        default:
          console.log(command);
          // console.log(scope)
          break;
      }
    },
    handleClick(row) {
      console.log(row);
    },
    filterStar(command) {
      this.starType = command;
    },
    filterStatus(command) {
      this.statusType = command;
    },
    filterTime(command) {
      this.timeType = command;
    },
    reFilter() {
      this.starType = "收藏";
      this.statusType = "状态";
      this.timeType = "修改时间";
      this.tableDataShow = this.tableData;
    },
    filterChange(value) {
      function filterValue(obj) {
        if (obj.name.includes(value)) return true;
        else return false;
      }
      if (value) {
        if (this.saveFlag == false) {
          this.searchTempTable = this.tableDataShow;
          this.saveFlag = true;
        }
        this.tableDataShow = this.tableDataShow.filter(filterValue);
      } else {
        this.saveFlag = false;
        this.tableDataShow = this.searchTempTable;
      }
    },
  },
  watch: {
    starType: function(newValue) {
      if (
        newValue == "收藏" &&
        this.statusType == "状态" &&
        this.timeType == "修改时间"
      ) {
        this.isFilter = true;
        this.usefulButton = "disabled";
      } else {
        this.isFilter = false;
        this.usefulButton = "new";
      }

      let statusType = this.statusType;
      function checkStar(obj) {
        if (statusType == "状态") return obj.star;
        else if (statusType == "暂停") {
          return obj.star && obj.state == 0;
        } else if (statusType == "进行中") {
          return obj.star && obj.state == 1;
        }
      }

      function checkNoStar(obj) {
        if (statusType == "状态") return !obj.star;
        else if (statusType == "暂停") {
          return !obj.star && obj.state == 0;
        } else if (statusType == "进行中") {
          return !obj.star && obj.state == 1;
        }
      }

      if (newValue == "已收藏")
        this.tableDataShow = this.tableData.filter(checkStar);
      else if (newValue == "未收藏") {
        this.tableDataShow = this.tableData.filter(checkNoStar);
      } else if (newValue == "收藏") {
        this.tableDataShow = this.tableData;
      }
    },
    statusType: function(newValue) {
      if (
        newValue == "状态" &&
        this.starType == "收藏" &&
        this.timeType == "修改时间"
      ) {
        this.isFilter = true;
        this.usefulButton = "disabled";
      } else {
        this.isFilter = false;
        this.usefulButton = "new";
      }

      let starType = this.starType;
      function checkStateGoing(obj) {
        if (starType == "收藏") return obj.state == 1;
        else if (starType == "已收藏") return obj.state == 1 && obj.star;
        else if (starType == "未收藏") return obj.state == 1 && !obj.star;
      }
      function checkStatePause(obj) {
        if (starType == "收藏") return obj.state == 0;
        else if (starType == "已收藏") return obj.state == 0 && obj.star;
        else if (starType == "未收藏") return obj.state == 0 && !obj.star;
      }

      if (newValue == "进行中") {
        this.tableDataShow = this.tableData.filter(checkStateGoing);
      } else if (newValue == "暂停") {
        this.tableDataShow = this.tableData.filter(checkStatePause);
      }
    },
    timeType: function(newValue) {
      if (
        newValue == "修改时间" &&
        this.starType == "收藏" &&
        this.statusType == "状态"
      ) {
        this.isFilter = true;
        this.usefulButton = "disabled";
      } else {
        this.isFilter = false;
        this.usefulButton = "new";
      }

      function MsgSortNow(arr) {
        arr.sort((a, b) => {
          let t1 = new Date(Date.parse(a.modifiedTime));
          let t2 = new Date(Date.parse(b.modifiedTime));

          return t2.getTime() - t1.getTime();
        });
        console.log(arr);
        return arr;
      }

      function MsgSortRev(arr) {
        arr.sort((a, b) => {
          let t1 = new Date(Date.parse(b.modifiedTime));
          let t2 = new Date(Date.parse(a.modifiedTime));

          return t2.getTime() - t1.getTime();
        });
        console.log(arr);
        return arr;
      }
      console.log(newValue);

      if (newValue == "时间倒序") {
        this.tableDataShow = MsgSortNow(this.tableDataShow);
      } else if (newValue == "时间正序") {
        this.tableDataShow = MsgSortRev(this.tableDataShow);
      } else if (newValue) {
        console.log("fefwsfe");
        this.tableDataShow = this.tableData;
      }

      console.log(this.isFilter);
    },
  },
  computed: {
    circleStatus: function() {
      return function(status) {
        let isGoing = status == 1;
        let isPause = status == 0;
        return {
          circle: true,
          going: isGoing,
          pause: isPause,
        };
      };
    },
    statusCom: function() {
      return function(status) {
        if (status == 0) {
          return "暂停";
        } else if (status == 1) {
          return "进行中";
        }
      };
    },
  },
};
</script>

<style lang="scss" scoped>
.rightBox {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 100px;
  color: #2a2a2a;
  font-size: 16px;
  .nameBox {
    width: 200px;
    text-align: right;
    margin-right: 25px;
  }
}

.container {
  margin: 0 2rem;
  min-width: 1100px;
  margin-top: 6vh;
  display: flex;
  flex-direction: column;
  .pageTitle {
    font-size: 20px;
    text-align: left;
    font-weight: 500;
    margin-bottom: 25px;
    font-family: PingFang SC;
  }
  .el-button--new {
    background-color: #5f7880;
    margin-left: 30px;
    .span {
      color: #ffffff;
    }
  }

  .el-button--disabled {
    background: #ededed;
    opacity: 0.5;
    border-radius: 5px;
    margin-left: 30px;
    .span {
      color: #2a2a2a;
    }
  }
  .el-table {
    margin-top: 30px;
    .el-icon-star-on {
      margin-right: 10px;
    }
    .red {
      color: #d9534f;
    }
    .blue {
      color: #00a0e9;
    }
  }
  .paginationBox {
    display: flex;
    flex-direction: row-reverse;
    height: 30px;
    .el-pagination {
      padding: 0;
    }
  }

  .table-box {
    padding: 10px 0px 50px 0px;
    margin-top: 26px;
    border-radius: 10px;
    background-color: #ffffff;
  }
}

.search-row-right {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 6.5%;
  .choose-line {
    display: flex;
    margin-left: 10%;
    padding-left: 5%;
    .el-dropdown {
      margin-right: 40px;
      cursor: pointer;
      min-width: 66px;
    }
    span {
      min-width: 80px;
      // display: flex;
      cursor: pointer;
    }
  }
  .button-box {
    // min-width: 250px;
  }
}

.container ::v-deep .el-table th div {
  color: #ffffff !important;
}

.container ::v-deep .el-button--text span {
  color: #00a0e9 !important;
}

.circle {
  border-radius: 50%;
  min-width: 10px;
  min-height: 10px;
}

.circle-box {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 24px;

  .going {
    background-color: #00a0e9;
  }
  .unpublished {
    background-color: #ababab;
  }
  .pause {
    background-color: #d9534f;
  }
  .finished {
    background-color: #5cb85c;
  }
}

.msgbox-delete {
  border-radius: 10px;
  display: flex;
  .msgbox-box {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    // justify-content: center;
    // padding-top: 30px;
    height: 130px;
    .msgbox-word {
      // width: 251px;
      // height: 23px;
      margin: auto;
      font-size: 18px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #575454;
    }
  }
}

@media screen {
}
</style>
